var Eve = require('./eve');

var Stack = require('./stack');

var Util = {
  // 
  isTouch: isTouch,
  isTransparent: isTransparent,
  isDrawableEl: isDrawableEl,
  getAdjustedRect: getAdjustedRect,
  getScale: getScale,
  // shim
  rAF: rAF(),
  cAF: cAF(),
  // EA
  Eve: Eve,
  // Stack
  Stack: Stack
};
/**
 * touch or NOT
 *
 * @return {Boolean}
 *     isTouch
 */

function isTouch() {
  return 'ontouchstart' in global.document;
}
/**
 * 
 * 
 *
 * @param {String} color
 *     
 */


function isTransparent(color) {
  color = color.replace(/\s/g, '');

  if (color === 'transparent') {
    return true;
  }

  var isRgbaOrHlsaTransparent = color.split(',')[3] === '0)';

  if (isRgbaOrHlsaTransparent) {
    return true;
  }

  return false;
}
/**
 * ctx.drawImage3
 *
 * @param {HTMLElement} el
 *     
 * @return {Boolean}
 *     
 *
 */


function isDrawableEl(el) {
  var isDrawable = ['img', 'canvas', 'video'].indexOf(el.tagName.toLowerCase()) !== -1;
  return isDrawable;
}
/**
 * 
 * getBoundingClientRect
 *
 * @param {HTMLElement} el
 *     
 * @return {Object}
 *     left / top
 *
 */


function getAdjustedRect(el) {
  var elRect = el.getBoundingClientRect();
  return {
    left: elRect.left + global.pageXOffset,
    top: elRect.top + global.pageYOffset
  };
}
/**
 * width/heightstylewidth/height
 * canvasstyle
 *
 * @param {HTMLElement} el
 *     
 * @return {Object}
 *     x / y
 *
 */


function getScale(el) {
  var elRect = el.getBoundingClientRect();
  return {
    x: el.width / elRect.width,
    y: el.height / elRect.height
  };
}
/**
 * requestAnimationFrameshim
 *
 */


function rAF() {
  return (global.requestAnimationFrame || global.webkitRequestAnimationFrame || global.mozRequestAnimationFrame || function (callback) {
    global.setTimeout(callback, 1000 / 60);
  }).bind(global);
}
/**
 * cancelAnimationFrameshim
 *
 */


function cAF() {
  return (global.cancelAnimationFrame || global.webkitCancelAnimationFrame || global.mozCancelAnimationFrame || function (callback) {
    global.clearTimeout(callback);
  }).bind(global);
}

module.exports = Util;